.work-list {
    background-color: #fff;
    margin: 100px 16px;
    display: flex;
    flex-wrap:wrap;
    justify-content:center;
    align-items:center;
    z-index: 100;
}
.work {
    background-color: #fff;
    margin: 50px 20px;
    display: flex;
    box-sizing: border-box;
    border:2px solid #ccc;
    width: 33.33333333%;
    height: 250px;
    border-radius: 40px;
}
/* iphone */
@media screen and (max-width: 420px) {
    .work-list {
    background-color: #fff;
    margin: 100px 16px;
    display: flex;
    flex-wrap:wrap;
    justify-content:center;
    align-items:center;
    z-index: 100;
    /* align-items: center; */
    }
    .work {
    background-color: #fff;
    /* padding: 10px 40px; */
    margin: 20px 20px;
    display: flex;
    box-sizing: border-box;
    border:2px solid #ccc;
    /* margin: 0; */
    /* padding: 20px; */
    width: 100%;
    height: 210px;
    border-radius: 40px;
    }
}
.work img {
    border-top-left-radius: 40px;
    border-bottom-left-radius: 40px;
    width: 40%;
    object-fit: cover;
}
.sentences {
    margin-left: auto;
    padding: 5px;
    width: 66%;
}
/* iphone */
@media screen and (max-width: 420px) {
    .sentences {
        margin-left: auto;
        padding: 5px;
        width: 66%;
        font-size: 12px;
    }
}
.pmwork {
    border-bottom: solid 8px #e6c5cf;
    position: relative;
    /* background: linear-gradient(transparent 50%, #e6c5cf 70%); */
}
.codework {
    border-bottom: solid 8px #a7d6ff;
    position: relative;
    /* background: linear-gradient(transparent 50%, #a7d6ff 70%); */
}
.pmwork:after {
    position: absolute;
    content: " ";
    display: block;
    border-bottom: solid 8px #ffc778;
    bottom: -8px;
    width: 30%;
}
.codework:after {
    position: absolute;
    content: " ";
    display: block;
    border-bottom: solid 8px #ffc778;
    bottom: -8px;
    width: 30%;
}
.logo {
    width: 500px;
    height: 390px;
}
