.App {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Hiragino Kaku Gothic Pro", "ヒラギノ角ゴ Pro W3", メイリオ, Meiryo, "ＭＳ Ｐゴシック", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

/* header */

header {
  padding: 10px 4% 30px;
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #fff;
  display: flex;
  align-items: center;
  z-index: 1000;
}
h1 {
  margin: 0;
  padding: 0;
  font-size: 20px;
  color: chocolate;
}
a {	
  text-decoration: none;
}
.pc-nav {
  margin: 0 0 0 auto;
  padding-right: 40px;
}
.pc-nav ul {
  list-style: none;
  margin: 0 100px 0 0;
  display: flex;
}
/* 現在のページ */
.active {
  background-color: #a7d6ff;
}
.pc-nav li {
  margin: 20px 10px 20px 10px;
  width: 60px;
  font-size: 14px;
  padding-left: 14px;
  color: #ffc778;

  position: relative;
  display: inline-block;
  text-decoration: none;
}
.pc-nav li::after {
  position: absolute;
  bottom: -4px;
  left: 0;
  content: '';
  width: 100%;
  height: 2px;
  background: #333;
  transform: scale(0, 1);
  transform-origin: right top;
  transition: transform .3s;
}
.pc-nav li:hover::after {
  transform-origin: left top;
  transform: scale(1, 1);
}

/* iphone */
@media screen and (max-width: 420px) {
  header {
    /* padding: 30px 4% 30px; */
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #fff;
    display: flex;
    align-items: center;
    z-index: 1000;
  }
  /* 現在のpage */
  .active {
    background-color: #a7d6ff;
  }
  h1 {
    margin: 0;
    padding: 0;
    font-size: 15px;
    color: chocolate;
  }
  a {	
    text-decoration: none;
  }
  .pc-nav {
    margin: 0 0 0 auto;
  }
  ul {
    list-style: none;
    margin: 0 0 0 0;
    display: flex;
  }
  /* 現在のページ */
  .active {
    background-color: #a7d6ff;
  }
  li {
    margin: 0 0 0 15px;
    font-size: 10px;
    color: #ffc778;
  }
}
